  }
  .temp{
    margin: 5px auto;
    font-weight: bold;
    font-size: calc(15px + 0.6vw + 0.4vh); 
    padding: 5px 66px;
    text-align: center;
  }
  .logo{
    position: relative;
    top: 5px;
    height: calc(40px + 6vw);
    width: calc(40px + 6vw);
    padding: 0;
    max-width: 80px;
    max-height: 80px;
  }
  .ligue{
    position: relative;
    bottom: 20px;
    font-size: calc(9px + 0.6vw);
  }

  .classement{
    font-size: calc(5px + 1vw);
  }
  .carton{
    font-size: calc(7px + 1vw);
  }
  
</style>



