<template>
	<div>
		<span class="updated" v-if="commentaires.updated">MAJ:{{commentaires.updated.substring(11, 19)}}</span>
    	<md-toolbar class="match" v-if="commentaires.match.statut == 'L'" :key="commentaires.match.id">
          <div style="width:100%;height:100%;">
              <div style="width:80%;height:100%;float:left;">
                  <div style="width:100%;float:left;">
                    <div style="width:42%;float:left;">
                      <span style="float:left;" class="classement">({{commentaires.match.recevant.classement}})</span>
                      <span style="float:left;" class="nom">&nbsp{{commentaires.match.recevant.club}}</span>
                    </div>
                    <div style="width:16%;float:left;">
                      <span style="width:40%;" class="score_commentaires">{{commentaires.match.recevant.score}}</span>
                      <span style="width:20%;" class="time">-</span>
                      <span style="width:40%;" class="score_commentaires">{{commentaires.match.visiteur.score}}</span>
                    </div>
                    <div style="width:42%;float:right;">
                      <span style="float:right;" class="classement">({{commentaires.match.visiteur.classement}})</span>
                      <span style="float:right;" class="nom">{{commentaires.match.visiteur.club}}&nbsp</span>
                    </div>
                  </div>
                  <br>
                  <div style="width:100%;" >
                    <span style="width:40%; float:left;" class="carton">
                      {{commentaires.match.recevant.nbRed}}
                      <img src="static/img/commentairesIcons/rouge.png" style="width:15px;height:15px;"> 
                      {{commentaires.match.recevant.nbYellow}}
                      <img src="static/img/commentairesIcons/jaune.png" style="width:15px;height:15px;">
                    </span>
                    <span style="width:20%; float:left;" class="ligue_commentaires">Ligue{{commentaires.match.ligue}}</span>
                    <span style="width:40%; float:left;" class="carton"> 
                      {{commentaires.match.visiteur.nbRed}}
                      <img src="static/img/commentairesIcons/rouge.png" style="width:15px;height:15px;"> 
                      {{commentaires.match.visiteur.nbYellow}}
                      <img src="static/img/commentairesIcons/jaune.png" style="width:15px;height:15px;">
                    </span>
                  </div>
              </div>
          </div>
        </md-toolbar>

        <md-toolbar class="match" v-if="commentaires.match.statut == 'P'" :key="commentaires.match.id">
          <div style="width:100%">
              <span style="float:left;" class="classement">({{commentaires.match.recevant.classement}})</span>
              <span style="float:left;" class="nom">&nbsp{{commentaires.match.recevant.club}}</span>
              <span class="temp_commentaires">{{commentaires.match.time}}</span>
              <span style="float:right;" class="classement">({{commentaires.match.visiteur.classement}})</span>
              <span style="float:right;" class="nom">{{commentaires.match.visiteur.club}}&nbsp</span>
              <div class="ligue_commentaires">Ligue{{commentaires.match.ligue}}</div>
          </div>
        </md-toolbar>

        <md-toolbar class="match" v-if="commentaires.match.statut == 'J'" :key="commentaires.match.id">
         <div style="width:100%;height:100%;">
              <span style="float:left;" class="classement">({{commentaires.match.recevant.classement}})</span>
              <span style="float:left;" class="nom">&nbsp{{commentaires.match.recevant.club}}</span>
              <span class="score_commentaires">{{commentaires.match.recevant.score}}</span>
              <span class="time">-</span>
              <span class="score_commentaires">{{commentaires.match.visiteur.score}}</span>
              <span style="float:right;" class="classement">({{commentaires.match.visiteur.classement}})</span>
              <span style="float:right;" class="nom">{{commentaires.match.visiteur.club}}&nbsp</span>
              <div>
              	{{commentaires.match.recevant.nbRed}}
                <img v-for="i in commentaires.match.recevant.nbRed" src="static/img/commentairesIcons/rouge.png" style="width:15px;height:15px;"> 
                {{commentaires.match.recevant.nbYellow}}
                <img src="static/img/commentairesIcons/jaune.png" style="width:15px;height:15px;">
                <span class="ligue_commentaires">Ligue{{commentaires.match.ligue}}</span>
                {{commentaires.match.visiteur.nbRed}}
                <img src="static/img/commentairesIcons/rouge.png" style="width:15px;height:15px;"> 
                {{commentaires.match.visiteur.nbYellow}}
                <img src="static/img/commentairesIcons/jaune.png" style="width:15px;height:15px;">
              </div>
          </div>
        </md-toolbar>
		<div>
			<table style="border:none;cellspacing:0;">
				<tbody>
					<tr v-for="commentaire in commentaires.comments" style="height:80px;width:100%;">
						<td v-if="commentaire.type === 'But'" width="2%" style="background-color:rgb(102, 153, 115)"></td>
						<td v-else width="2%" style="background-color: rgb(194, 194, 214)"></td>
						<td width="18%" style="background-color:rgb(224, 224, 235);position:relative;">
							<div class="minute" v-if="commentaire.minute && commentaire.type !=='bla'">{{commentaire.minute}}'</div>
							<div class="minute_middle" v-if="commentaire.minute && commentaire.type ==='bla'">{{commentaire.minute}}</div>
							<div class="type">
								<img v-if="commentaire.type === 'But'"src="/static/img/commentairesIcons/ball.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'CRg'"src="/static/img/commentairesIcons/rouge.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'CJn'"src="/static/img/commentairesIcons/jaune.png" style="width:20px;height:20px;">
								<img v-if="commentaire.type === 'Rpl'"src="/static/img/commentairesIcons/change.png" style="width:20px;height:20px;">
							</div>
						</td>
						<td width="80%">
							<div class="text" v-if="commentaire.type ==='But' || commentaire.type === 'Pen'"style="font-weight:bold;text-align:left;">{{commentaire.text}}</div>
							<div class="text" v-else style="font-weight:normal;text-align:left;">{{commentaire.text}}</div>
						</td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</template>

<script>
var debounce = require('lodash.debounce')

export default {
  name: 'commentaires',

  data () {
    return {
      commentaires: '',
      commentaire: ''
    }
  },
  methods: {
    updateCommentaires: debounce(function () {
      if (this.$localStorage.get('id')) {
        this.$http.get('https://qlf-www-lfp-fr.aw.atos.net/ws/comments.json?id=' + this.$localStorage.get('id')).then(response => {
        // json success call back
          this.commentaires = response.data
          this.$localStorage.set('commentaires', JSON.stringify(response.data))
        }, response => {
          // error callback
          console.log('error call data')
        })
      } else {
        this.commentaires = JSON.parse(this.$localStorage.get('commentaires'))
      }
    }, 1000)
  },

  watch: {
    matches: function () {
      this.updateCommentaires()
    }
  },

  created: function () {
    this.updateCommentaires()
    window.addEventListener('online', this.updateCommentaires)
  }
}
</script>

<style>
.updated{
  float: right;
  position: relative;
  bottom: 20px;
}
.score_commentaires{
	margin: 5px auto;
	font-weight: bold;
	font-size: calc(22px + 0.6vw + 0.4vh); 
	padding: 5px 0;
	padding:0 10px;
	vertical-align:middle;
}
.classement{
   font-size: calc(8px + 0.7vw);
}
.nom{
	font-weight: bold;
   font-size: calc(10px + 0.7vw);	
}
.ligue_commentaires{
	margin: auto;
}
.temp_commentaires{
	margin: auto;
	font-weight: bold;
	font-size: calc(10px + 0.6vw + 0.4vh); 
	padding: 5px 0px;
	text-align: center;
}
body,html {
	margin: 0;
	width: 100%;
	height: 100%;
}
.commentaires{
	min-height: 80px;
}
.commentaire{
	display: flex;
	min-height: 100px;
	width: 100%;
	margin: 10px 0;
}
.left-border{
	background-color: rgb(194, 194, 214);
}
.minute{
	font-weight: bold;
	font-size: 20px;

	position: absolute; 
	width:100%;
	top:0;
	left:0;
	text-align: center;
}
.minute_middle{
	font-weight: bold;
	font-size: 20px;

	position: absolute; 
	width:100%;
	top:40%;
	left:0;
	text-align: center;
}
.type{
	position: absolute;
	width:100%;
	top:30px;
	left:0;
	text-align: center;
}
.right{
	min-height: 80px;
	border-bottom: 1px dashed rgb(194, 194, 214);
}
.text{
	display: flex;
	padding: 10px 0;
	line-height: 120%;
	font-size: calc(12px + 0.6vw + 0.4vh);
}
td{border-bottom:1px solid rgb(224, 224, 235)}
</style>


